.hide {
  display: none; }


.header {
  background-color: white !important; }
  .header .header-start {
    flex: 1 1;
    font-family: 'Open Sans', sans-serif;
    display: flex;
    align-items: center;
    justify-content: flex-start; }
  .header .header-end {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    .header .header-end .user-name {
      margin-right: 15px;
      cursor: default; }
  .header .user-actions .action {
    font-weight: 700;
    margin-right: 15px; }
    .header .user-actions .action :first-child {
      margin-right: 5px;
      margin-left: 5px; }
  .header .user-actions .user-register {
    color: green;
    background-color: white; }
  .header .user-actions .user-login {
    color: green; }
  .header .user-actions .user-logout {
    font-weight: 300;
    font-size: 0.75rem;
    margin-left: 2rem;
    color: crimson; }
  .header .user-actions .new-product {
