.bill-container {
  text-align: left;
  padding: 1rem; }
  .bill-container .bill-label {
    font-size: 0.75rem; }
  .bill-container .agreement {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: whitesmoke; }
  .bill-container .agreed span {
    font-size: 0.75rem; }
  .bill-container .bill-actions button {
    width: 100%; }

.checkout-container .checkout-content .checkout-title {
  font-size: 1.5rem;
  font-weight: 700;
  text-align: left; }

.catalogue {
  flex: 1 1;
  min-height: 100vh;
  margin: 15px; }
  .catalogue .tab-content {
    min-height: 80vh;
    flex: 1 1; }
  .catalogue .catalogue-content {
    display: flex;
    justify-content: center; }
    .catalogue .catalogue-content .list-child {
      cursor: default; }
  .catalogue .catalogues-tabs {
    width: 100%; }
  .catalogue .children-grid {
    margin-top: 1rem; }
  .catalogue .category-tab {
    display: flex;
    justify-content: center; }
  .catalogue .product-card {
    background-color: whitesmoke; }
    .catalogue .product-card .product-header {
      padding: 10px;
      padding-bottom: 5px; }
    .catalogue .product-card .product-content {
      background-color: white;
      text-align: left;
      display: flex;
      flex-direction: column; }
      .catalogue .product-card .product-content :nth-child(1) {
        font-size: 1.25rem;
        font-weight: 900; }
      .catalogue .product-card .product-content :nth-child(2) {
        font-size: 0.875rem;
        font-weight: 300; }
    .catalogue .product-card .product-actions {
      align-items: stretch;
      justify-content: space-around; }
      .catalogue .product-card .product-actions :nth-child(1) {
        flex: 2 1;
        display: flex;
        justify-content: space-around;
        align-items: center; }
        .catalogue .product-card .product-actions :nth-child(1) > span {
          flex: 1 1;
          font-size: 0.75rem;
          color: gray; }
        .catalogue .product-card .product-actions :nth-child(1) > svg {
          flex: 1 1;
          color: green;
          cursor: pointer; }
        .catalogue .product-card .product-actions :nth-child(1) input {
          text-align: center;
          background-color: white; }
      .catalogue .product-card .product-actions :nth-child(2) {
        flex: 1 1;
        display: flex;
        justify-content: space-evenly; }
        .catalogue .product-card .product-actions :nth-child(2) button {
          flex: 1 1;
          min-width: 2rem;
          padding: 0;
          font-weight: 900;
          border: none; }
          .catalogue .product-card .product-actions :nth-child(2) button.cart-add {
            background-color: rgba(0, 128, 0, 0.7); }
            .catalogue .product-card .product-actions :nth-child(2) button.cart-add svg {
              color: white; }
          .catalogue .product-card .product-actions :nth-child(2) button.cart-remove svg {
            color: crimson; }

.product-modal {
  display: flex;
  align-items: center;
  justify-content: center; }
  .product-modal .product-form-container {
    min-width: 50vw;
    width: 100%;
    outline: none; }
    .product-modal .product-form-container .modal-card {
      background-color: green; }
      .product-modal .product-form-container .modal-card .form-actions {
        justify-content: flex-end; }
        .product-modal .product-form-container .modal-card .form-actions button {
          flex: 0.4 1;
          color: white;
          font-weight: 900; }
    .product-modal .product-form-container .form-header {
      display: flex;
      justify-content: space-between;
      align-items: flex-start; }
      .product-modal .product-form-container .form-header .form-title {
        font-family: 'Open Sans', sans-serif;
        text-align: center;
        color: white;
        font-size: 1.5rem;
        font-weight: 900; }
      .product-modal .product-form-container .form-header svg {
        text-align: right;
        cursor: pointer;
        color: white;
        border: none;
        background-color: firebrick; }
    .product-modal .product-form-container .product-form {
      margin-top: 2rem; }
      .product-modal .product-form-container .product-form > div {
        margin-top: 2rem; }
        .product-modal .product-form-container .product-form > div input {
          font-size: 1rem;
          line-height: 1rem;
