      .product-modal .product-form-container .product-form .part-1 {
        display: flex;
        justify-content: space-between; }
        .product-modal .product-form-container .product-form .part-1 .product-name {
          flex: 4 1;
          margin-right: 10%; }
        .product-modal .product-form-container .product-form .part-1 .product-qty {
          flex: 1 1; }
      .product-modal .product-form-container .product-form .multiline {
        font-size: 1rem;
        line-height: 1.2rem;
        padding: 0.6rem 1.2rem;
        border: lightgray 1px solid;
        border-radius: 5px; }
      .product-modal .product-form-container .product-form button {
        margin-top: 2rem;
        background-color: whitesmoke;
        color: green;
        font-weight: 900;
        flex: 1 1; }

.dashboard-container .loading-screen {
