  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }
  .dashboard-container .loading-screen > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.8); }
    .dashboard-container .loading-screen > div > svg {
      color: green; }

.home-container .hero {
  min-height: 100vh;
  max-width: 100vw;
  overflow-x: hidden;
  background: url("/toolocity-banner.jpg") no-repeat center;
  background-size: cover; }
  .home-container .hero > div {
    padding-top: 10vh;
    padding-left: 5vw; }
  .home-container .hero .hero-text {
